dl, ol, ul {
    margin-top: 0;
    margin-bottom: 1rem;
    list-style: none;
    margin: 0px;
    padding: 0px;
}
a{
    text-decoration: none;
    color: inherit;
}
a:hover{
    text-decoration: none;
    color: inherit;
}

.header{
    width: 100%;
    background-color: #fff;
}
.container{
    padding: 0px !important;
}
.header .header-main{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.header .logo {
    width: 25%;
}
.header .search{
    width: 50%;
}
.header .header-items{
    width: 25%;
    height: 50px;
    text-align: center;
    background-image: url('../../assets/icons.png');
    background-size: 230px 3243px;
    background-repeat: no-repeat;
    background-position: 50px -2427px;
}
.header .header-items:hover{
    background-position: 50px -2472px;
}
.header .header-items .right-cart-number{
    background: #ff4747;
    max-width: 25px;
    min-width: 20px;
    height: 20px;
    color: #fff;
    border-radius: 10px;
    position: absolute;
    top: 20px;
    margin-left: -72px;
    font-size: 12px;
    line-height: 20px;
    text-align: center;
    padding: 0 2px;
}

.header .logo img{
    width: 200px;
    height: 80px;
    object-fit: contain;
    margin-left: 20px;
}
.header .search{
    display: flex;
}
.header .search .search-input{
    width: 100%;
    border: 2px solid #ff4747;
    padding: 11px 10px 11px 0;
    line-height: 18px;
    border-right: 0px;
    font-size: 13px;
    text-indent: 20px;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    background-image: url('../../assets/search.png');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
.header .search select {
    border-top: 2px solid #ff4747;
    border-bottom: 2px solid #ff4747;
    border-left: 2px solid #eee;
    padding: 5px;
}
.header .search-button{
    width: 60px;
    background-color: #ff4747;
    border-radius: 0 4px 4px 0;
    border: 2px solid #ff4747;
}


/* Home Page  First Screen */

.home-page{
    background-color: #f2f2f2;
}
.home-page .first-screen{
    padding: 20px 0px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}
.home-page .first-screen .categories{
    width: 25%;
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
}
.home-page .first-screen .advertise{
    width: 50%;
    margin: 0px 10px;
    background-color: #fff;
    border-radius: 8px;
}
.home-page .first-screen .account-main{
    width: 25%;
    background-color: #fff;
    background-image: url("https://ae01.alicdn.com/kf/He1ffbc1d340f4be8a0a6d07641c58180t.png_.webp");
    background-repeat: no-repeat;
    background-size: 100% 109px;
    padding: 20px;
    border-radius: 8px;
}

.home-page .first-screen .categories .categories-title a{
    display: flex;
    justify-content: left;
    color: inherit;
    padding-bottom: 10px;
    margin-left: 5px;
}
.home-page .first-screen .categories .categories-title a:hover{
    text-decoration: none;
}
.home-page .first-screen .categories .categories-title a h3{
    font-size: 14px;
    font-weight: 700;
    padding-left: 20px;
}

.home-page .first-screen .advertise{
    display: flex;
    flex-direction: column;
}
.home-page .first-screen .advertise .banner{
    height: 50%;
}
.home-page .first-screen .advertise .banner img{
    width: 100%;
    height: 228px !important;
    object-fit: contain;
    border-radius: 10px;
}
.home-page .first-screen .advertise .banner .slick-dots{
    position: absolute;
    bottom: 7px;
}
.home-page .first-screen .advertise .banner .slick-dots li button:before {
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    width: 6.4px;
    height: 6.4px;
    border-radius: 50%;
    text-align: center;
    background: #fff;
}
.home-page .first-screen .advertise .banner .slick-dots li button {
    font-size: 0;
    line-height: 0;
    display: block;
    width: 6.4px;
    height: 6.4px;
    padding: 0;
    cursor: pointer;
    color: #fff;
    border: 0;
    outline: 0;
    background: 0 0;
}
.home-page .first-screen .advertise .banner .slick-dots li.slick-active button:before {
    width: 12.8px;
    border-radius: 3.2px;
    opacity: 1;
    background: #ff472e;
}
.home-page .first-screen .categories .categories-list-box ul a{
    color: #000;
    text-decoration: none;
}
.home-page .first-screen .categories .categories-list-box ul a:hover{
    color:#e62e04;
    font-weight: 600;
}
.home-page .first-screen .categories .categories-list-box ul .item{
    display: flex;
    flex-direction: row;
    padding: 6px 0px;
}
.home-page .first-screen .categories .categories-list-box ul .item .categories-icon{
    width: 30px;
    height: 24px;
}
.slick-dots li{
    width: 15px;
    height: 15px;
}

.ranking{
    padding: 20px;
}
.ranking-wrapper{
    display: flex;
    justify-content: space-evenly;
}
.ranking-wrapper .ranking-products{
    width: 25%;
    padding: 8px;
}
.ranking-wrapper .ranking-products .ranking-image img{
    width: 100%;
    height: 120px;
    object-fit: contain;
}
.ranking-wrapper .ranking-products .ranking-title h5{
    text-align: center;
    font-size: 13px;
    padding-top: 10px;
    font-weight: 700;
}
.ranking-wrapper .react-loading-skeleton:not(:last-child){
    margin-right: 10px;
}
.ranking-wrapper .ranking-loading{
    display: flex;
    flex-direction: column;
}

/* Account in Firt Section */
.home-page .first-screen .account-main .main{
    text-align: center;
}
.home-page .first-screen .account-main .main .user img{
    width: 50px;
    height: 50px;
}
.home-page .first-screen .account-main .main .user h5{
    line-height: 18px;
    font-size: 15px;
    font-weight: 700;
    color: #000;
    margin-top: 20px;
}
.home-page .first-screen .account-main .main .buttons{
    display: flex;
    justify-content: space-evenly;
    padding-top: 35px;
}
.home-page .first-screen .account-main .main .buttons button{
    border: 0px;
    padding: 5px 20px;
    border-radius: 20px;
    font-size: 14px;
    font-weight: 600;
}
.home-page .first-screen .account-main .main .buttons button:first-child{
    background-image: linear-gradient(94deg,#ff0a0a,#ff7539);
    color: #fff;
}




/* Home Page  Second Screen */

.home-page .second-screen {
    margin-top: 40px;
}
.home-page .second-screen .section-title{
    text-align: center;
    padding-bottom: 20px;
}
.home-page .second-screen .products{
    display: flex;
    flex-direction: row;
    width: 100%;
    flex-wrap: wrap;
}
.home-page .second-screen .products .product-item{
    width: 15.6%;
    background-color: #fff;
    margin: 0 12px 16px 0;
    border-radius: 8px;
    height: 230px;
}
.home-page .second-screen .products .product-item:hover {
    box-shadow: 0 1px 10px rgb(0 0 0 / 12%);
}
.home-page .second-screen .products .product-item .product-image img{
    width: 100%;
    height: 190px;
    object-fit: contain;
    height: 150px;
    padding: 10px;
}
.home-page .second-screen .products .product-item .product-content{
    padding: 10px;
}
.home-page .second-screen .products .product-item .product-content .product-title{
    font-size: 12px;
    color: #000;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.home-page .second-screen .products .product-item .product-content .product-price{
    font-weight: 700;
    font-size: 22px;
    color: #000;
}

.home-page .second-screen .products .product-item a:hover{
    text-decoration: none !important;
}

/* Modal Login */

.modal-login{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding: 20px 0;
}
.modal-login .modal-logo img{
    width: 130px;
    height: 40px;
    object-fit: contain;
}
.modal-login form{
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 30px;
}
.modal-login form .modal-input{
    width: 100%;
    height: 40px;
    padding: 0 16px 0 10px;
    border: 1px solid #ebebeb;
    border-radius: 4px;
    font-size: 14px;
    color: #333;
    margin-bottom: 20px;
}
.modal-login form .modal-submit{
    height: 44px;
    background: #ff4747;
    border: 0;
    border-radius: 3px;
    font-size: 14px;
    line-height: 44px;
    color: #fff;
    text-transform: uppercase;
}
.modal-login .forgot-password a{
    height: 18px;
    font-size: 12px;
    color: #2e9cc3;
    text-align: left;
}
.modal-login .forgot-password{
    width: 100%;
    margin-top: 15px;
}


/* Single */
.single .product-main{
    background-color: #fff;
    padding-top: 30px;
    border-top: 2px solid #f2f2f2;
}
.single .product-main .product{
    display: flex;
    flex-direction: row;
}
.single .product-main .product .product-image{
    width: 40%;
}
.single .product-main .product .product-image img{
    width: 100%;
    height: 350px;
    object-fit: contain;
}
.single .product-main .product .product-content .product-title h3{
    font-size: 14px;
    line-height: 19px;
}
.single .product-main .product .product-content .product-price h3{
    font-size: 24px;
    font-weight: bold;
    padding-bottom: 10px;
    border-bottom:1px solid #f2f2f2;
}
.single .product-main .product .product-content .product-colors ul{
    display: flex;
    justify-content: space-between;
    padding: 20px 0px;
}
.single .product-main .product .product-content .product-colors ul li  img{
    border: 1px solid #f1f1f1;
}
.single .product-main .product .product-content .product-ships {
    padding: 15px 0px;
}
.single .product-main .product .product-content .product-ships div{
    padding: 10px 0px;
}
.single .product-main .product .product-content .product-ships button{
    background-color: transparent;
    border: 1px solid #000;
    margin-right: 20px;
}


/* Footer */

.footer{
    background-color: #e8e8e8;
    padding-top: 30px;
    margin-top: 30px;
}

.footer .footer-content{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.footer .footer-content .footer-item{
    width: 50%;
    padding: 20px;
}
.footer .footer-content .footer-item h3{
    font-size: 16px;
    color: #333;
    line-height: 28px;
    margin-bottom: 5px;
}
.footer .footer-content .footer-item span{
    color:#999;
}
    
   